/**
 * modal api
 */

export default {
  methods: {
    /**
     *  Full 
     */
    clickFullNav() {
      if (this.commit) {
        this.no()
      } else {
        this.hide()
      }
    },

    /**
     * pop
     *
     * @param {Number} - 
     * @return {Object}
     */
    show() {
      this.modalDisplay = true
      this.$refs.pop.show()

      return this
    },

    /**
     * pop
     *
     * @return {Object}
     */
    hide() {
      this.$refs.pop.hide({
        cb: () => {
          this.modalDisplay = false
          this.isMousedown = false
        }
      })

      return this
    },

    /**
     * mouseDown 
     *
     * @return {Object}
     */
    mouseDown(event) {
      this.isMousedown = true

      this.pointStart = {
        x: event.clientX,
        y: event.clientY
      }

      return this
    },

    /**
     * mouseMove 
     *
     * @return {Object, Boolean}
     */
    mouseMove(event) {
      event.preventDefault()

      if (!this.isMousedown) {
        return false
      }

      let $this = this.$el.querySelector('.' + this.xclass('pop'))
      let styleHub = getComputedStyle($this)
      let top = parseFloat(styleHub.top, 10)
      let left = parseFloat(styleHub.left, 10)

      this.$refs.pop.position({
        top: top + event.clientY - this.pointStart.y,
        left: left + event.clientX - this.pointStart.x
      })

      this.pointStart = {
        x: event.clientX,
        y: event.clientY
      }

      return this
    },

    /**
     * mouseUp 
     *
     * @return {Object, Boolean}
     */
    mouseUp(event) {
      event.preventDefault()

      if (!this.isMousedown) {
        return false
      }

      this.isMousedown = false

      return this
    },

    /**
     * 
     *
     * @return {Object}
     */
    ok() {
      if (this.okCb) {
        this.okCb(this)

        return this.$emit('ok')
      }

      this.hide()
    },

    /**
     * 
     *
     * @return {Object}
     */
    no() {
      if (this.noCb) {
        this.noCb(this)

        return this.$emit('no')
      }

      this.hide()
    },

    /**
     *  /  title
     *
     * @return {Object, Boolean}
     */
    title(text) {
      if (text === '' || text) {
        this.modalHeader = text
      }

      return this
    },

    /**
     * alert, confirm 
     *
     * @param {String} - 
     * @return {Object, String}
     */
    info(text) {
      if (text === '' || text) {
        this.modalMessage = text
      }

      return this
    },

    /**
     * 
     *
     * @param {Object} opt - 
     *                       {Function} okCb - 
     *                       {Function} noCb - 
     *                       {String} title - 
     *                       {Function} message - 
     */
    set({
      okCb,
      noCb,
      title = '',
      message = ''
    } = {}) {
      this.okCb = okCb
      this.noCb = noCb
      this.modalHeader = title
      this.modalMessage = message

      return this
    }
  }
}


