/**
 * message 
 *
 * @prop message - 
 * @prop direction - 
 * @prop type - (pop | bar | header)
 *
 * @slot - 
 *
 * @event show - 
 * @event hide - 
 */

import './message.scss'
import './message.m.scss'

import render from './message.render'
import baseMixin from '../../../mixin/base'

import popComp from '../../base/pop/pop'
import btnComp from '../../base/btn/btn'
import iconComp from '../../base/icon/icon'

const TIP_DISPLAY_TIME = 1500

const messageComp = {
  name: 'message',

  render,

  mixins: [baseMixin],

  components: {
    btn: btnComp,
    icon: iconComp,
    pop: popComp
  },

  computed: {
    // 
    cPrefix() {
      return `${this.compPrefix}-message`
    },
    //  header  class 
    headerClass() {
      return {
        [`${this.cPrefix}-no-header`]: !this.headerDisplay,
        [`${this.cPrefix}-no-header-title`]: !this.popHeaderName
      }
    },
    //  footer  class 
    footerClass() {
      return { [`${this.cPrefix}-no-footer`]: !this.footerDisplay }
    }
  },

  props: {
    type: {
      type: String,
      default: 'pop'
    },
    direction: {
      type: String,
      default: 'top'
    },
    message: {
      type: String,
      default: ''
    }
  },

  data: () => {
    return {
      // 
      infoMessage: '',
      // 
      messageType: '',
      messageDisplay: false,
      hideCb: null
    }
  },

  methods: {
    _init() {
      this._initmessage()
    },

    _initmessage() {
      this.$refs.pop.initPosition()
    },

    /**
     * 
     */
    _setDataOpt() {
      this.infoMessage = this.message
    },

    /**
     * 
     *
     * @param {Object} opt - 
     *                       {Function} cb - 
     * @return {Promise}
     */
    show({ cb } = {}) {
      return this.$nextTick(() => {
        this.messageDisplay = true

        this.$refs.pop.show({
          cb: () => {
            setTimeout(() => {
              this.hide()
            }, TIP_DISPLAY_TIME)

            cb && cb()

            return this.$emit('hide')
          }
        })

        return this
      })
    },

    /**
     * pop
     *
     * @param {Object} opt - 
     *                       {Function} cb - 
     * @return {Object}
     */
    hide({ cb } = {}) {
      this.$refs.pop.hide({
        cb: () => {
          this.messageDisplay = false
          this.isMousedown = false

          this.hideCb && this.hideCb()
          cb && cb()

          return this.$emit('hide')
        }
      })

      return this
    },

    /**
     * alert, confirm 
     *
     * @param {String} - 
     * @return {Object, String}
     */
    info(text) {
      if (text === '' || text) {
        this.infoMessage = text
      }

      return this
    },

    /**
     * 
     *
     * @param {Object} opt - 
     *                       {Function} hideCb - 
     *                       {String} type - 
     *                       {Function} message - 
     */
    set({
      hideCb,
      type = 'pop',
      message = ''
    } = {}) {
      this.infoMessage = message
      this.hideCb = hideCb
      this.messageType = type

      return this
    }
  }
}

export default messageComp



