/**
 * transition  mixin
 *
 * @prop speed - 
 *
 * @event beforeEnter - 
 * @event enter - 
 * @event afterEnter - 
 * @event beforeLeave - 
 * @event leave - 
 * @event afterLeave - 
 */

export default {
  props: {
    speed: {
      type: [Number, String],
      default: 'normal'
    }
  },

  computed: {
    time() {
      switch (this.speed) {
        case 'normal':
          return 300
        case 'fast':
          return 150
        case 'slow':
          return 450
        default:
          return 300
      }
    },
    transitionTime() {
      return this.time + 'ms'
    }
  }
}


